/* mystyle */

@font-face{
    font-family: 'Consolas';
    font-weight: normal;
    font-style: normal;

    src: url('/__folder/font/consola.woff') format('woff'),
    url('/__folder/font/consola.ttf') format('truetype');
}
body{padding-top: 50px;}

a[target]{
    display: inline-block;
    margin: 0 0.5em;

    text-decoration: underline;
    text-indent: 0;

    color: blue;
}

a[target]:hover{
    color: @primary-dark;
}

.hasNav{
    padding: 50px 0 0 200px;
}

.hasNav .wrap{
    margin-bottom: 2em;
}

.hasNav p{
    margin: 1em 0;

    text-indent: 2em;
}
.hasNav ol li{
    margin: 1em 0;
}
.hasNav h2{
    margin: 1em 0;

    color: @primary;
}
.hasNav h3{
    line-height: 2;

    color: @black;
}
.hasNav h4{
    line-height: 1.43;
    margin: 1em 0;
    padding: 0 0.5em;

    color: @black;
    border-left: 4px solid @gray-lighter;
}
.hasNav h5{
    margin: 1em 0;

    color: @black;
}


#nav{
    overflow: auto;
    position: fixed;
    top: 0;
    left: 0;
    width: 160px;
    height: 100%;
    padding: 50px 10px 0 30px;
    border-right: 1px solid @gray-lighter;
}
#nav li{
    line-height: 2.3;
    margin: 1em 0;
}
#nav li a{
    font-weight: bold;
}
#nav li li{
    margin: 0;
}
#nav li li a{
    display: block;
    font-weight: normal;
    line-height: 2;
    height: 2em;
    padding-left: 1em;

    color: #1364c4;
    border-radius: 4px;
}
#nav li li a:hover{
    color: #333;
    background: @gray-lighter;
}

.modules_index{
    overflow: hidden;
    font-size: 14px;

    border-right: 1px solid @gray-lighter;
    border-bottom: 1px solid @gray-lighter;
}
.modules_index a{
    display: block;
    line-height: 3em;
    margin: 0 -1px -1px 0;

    text-align: center;

    color: #3e454c;
    border: 1px solid @gray-lighter;
}
.modules_index a:hover{
    background: @gray-lighter;
}

.modules_list .table th:first-child,.modules_list .table td:first-child{
    width: 10em;
}

.demo p{
    text-indent: 0;
}

.hasNav footer{
    margin-bottom: 0 !important;
    padding: 0;

    border-top: 1px solid #7ecefd;
}

.foot_logo{
    position: fixed;
    right: 2em;
    bottom: 2em;
    line-height: 4em;
    width: 50px;
    height: 4em;

    text-align: center;
    vertical-align: middle;
}
.foot_logo figure{
    display: inline-block;
    width: 50%;
    height: 25px;
    margin: 0;

    animation-name: foot-logo;
    animation-duration: 5s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    vertical-align: middle;

    transform-style: preserve-3d;
}

.hasNav .modules_list>dt{
    position: relative;
    font-size: 26px;
    font-weight: normal;

    color: @primary;
}
.hasNav .modules_list>dt:after{
    position: absolute;
    top: 0.2em;
    right: 100%;

    content: '';

    opacity: 0.5;
}
.hasNav .modules_list>dd{
    margin-bottom: @space-lg;
}

@keyframes foot-logo{
    0%{
        width: 73%;

        transform: rotateY(0deg);

        border-radius: 2px;
        background: @primary;
    }
    23%{
        width: 73%;

        transform: rotateY(0deg);

        border-radius: 2px;
        background: @primary;
    }
    33%{
        width: 68%;

        transform: rotateY(180deg);

        border-radius: 6px;
        background: #7ecefd;
    }
    56%{
        width: 68%;

        transform: rotateY(180deg);

        border-radius: 6px;
        background: #7ecefd;
    }
    66%{
        width: 35%;

        transform: rotateY(360deg);

        border-radius: 2px;
        background: #3e454c;
    }
    89%{
        width: 35%;

        transform: rotateY(360deg);

        border-radius: 2px;
        background: #3e454c;
    }
    100%{
        width: 73%;

        transform: rotateY(540deg);

        border-radius: 2px;
        background: @primary;
    }
}

/* pack code theme */
.hasNav pre{
    display: block;
    overflow: auto;
    font-size: 0;
    max-height: 500px;
    margin: @space 0 @space-lg;

    background: #343434;
}

.hasNav code{
    display: inline-block;
    font-family: 'Consolas', 'Microsoft YaHei UI', 'Microsoft YaHei', Arial, Helvetica, sans-serif ;
    font-size: 14px;
    margin: 0 0.5em;
    padding: 0 0.6em;

    text-indent: 0;

    color: #c7254e;
    background: #f0f0f0;
}
.hasNav pre code{
    display: none;

    background: none;
}
.hasNav .hljs{
    display: block !important;
    line-height: 1.4em;
    margin: 0;
    padding: 0 2em;

    color: inherit;
    color: #ddd;
    border: 0;
    border-radius: 0;
}
.hljs-comment,.hljs-template_comment,.diff .hljs-header,.hljs-javadoc{
    font-style: italic ;

    color: #aeaeae;
}
.hljs-keyword,.css .rule .hljs-keyword,.hljs-winutils,.javascript .hljs-title,.nginx .hljs-title,.hljs-subst,.hljs-request,.hljs-status{
    color: #fbde2d;
}
.hljs-number,.hljs-hexcolor,.ruby .hljs-constant{
    color: #c82829;
}
.hljs-string,.hljs-tag .hljs-value,.hljs-phpdoc,.tex .hljs-formula{
    color: #61ce3c;
}
.hljs-title,.hljs-id,.coffeescript .hljs-params,.scss .hljs-preprocessor{
    font-weight: bold;

    color: #900;
}
.javascript .hljs-title,.lisp .hljs-title,.clojure .hljs-title,.hljs-subst{
    font-weight: normal;
}
.hljs-class .hljs-title,.haskell .hljs-type,.vhdl .hljs-literal,.tex .hljs-command{
    font-weight: bold;

    color: #4271ae;
}
.hljs-tag,.hljs-tag .hljs-title,.hljs-rules .hljs-property,.django .hljs-tag .hljs-keyword{
    font-weight: normal;

    color: #8da6ce;
}
.hljs-attribute,.hljs-variable,.lisp .hljs-body{
    color: #ddd;
}
.hljs-regexp{
    color: #009926;
}
.hljs-symbol,.ruby .hljs-symbol .hljs-string,.lisp .hljs-keyword,.tex .hljs-special,.hljs-prompt{
    color: #990073;
}
.hljs-built_in,.lisp .hljs-title,.clojure .hljs-built_in{
    color: blue;
}
.hljs-preprocessor,.hljs-pragma,.hljs-pi,.hljs-doctype,.hljs-shebang,.hljs-cdata{
    font-weight: bold;

    color: #999;
}
.hljs-deletion{
    background: #fdd;
}
.hljs-addition{
    background: #dfd;
}
.diff .hljs-change{
    background: #0086b3;
}
.hljs-chunk{
    color: #aaa;
}

