.doc-head{
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    line-height: 50px;
    width: 96%;
    height: 50px;
    padding: 0 2%;

    color: #fff;
    background: #21374b;
}
.doc-head h1{
    float: left;
    font-size: 22px;
    line-height: inherit;
    height: 100%;
    margin: 0;
}
.doc-head ._link{
    float: right;
    height: 100%;

    color: #fff;
}
.doc-head ._link:hover{
    text-decoration: none;

    color: #eee;
}
.doc-head ._nav{
    float: left;
    font-size: 16px;
    margin: 0 1em;
}
.doc-head ._nav li{
    float: left;
    margin: 0 0.5em;
}
